<!--  -->
<template>
  <div>
    <common-head :headerCofig="headerCofig"></common-head>
    <!-- Swiper -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <nowTrends></nowTrends>
        </div>
        <div class="swiper-slide">
          <basic></basic>
        </div>
        <div class="swiper-slide">
          <market></market>
        </div>
         <div class="swiper-slide">
          <splitting></splitting>
        </div>
      </div>
    </div>
    <div class="colee-box">
      <Colee></Colee>
    </div>
  </div>
</template>

<script>
import Swiper from "swiper";
import commonHead from "@/components/header.vue";
import Colee from "@/components/colee.vue";
import nowTrends from "@/pages/index/views/nowTrends.vue";
import basic from "@/pages/index/views/basic.vue";
import market from "@/pages/index/views/market.vue";
import splitting from "@/pages/index/views/splitting.vue";
export default {
  name: "index",
  data() {
    return {
      coleeList: [],
      headerCofig: {
        title: "",
        time: ""
      },
      slideIndex: "",
      swiper: {}
    };
  },
  components: {
    Colee,
    commonHead,
    nowTrends,
    basic,
    market,
    splitting
  },
  methods: {
    getNowDate() {
      let date = new Date();
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    setHeaderConfig(index) {
      this.$x.get(this.url.climateIndex, {}).then(res => {
        let routerList = ["nowTrends", "basic", "market", "splitting"];
        let titleList = [
          "原油市场今日动态",
          "原油基本面分析",
          "原油市场行情趋势",
          "裂解价差"
        ];
        // let routerName = window.location.hash.slice(2, location.hash.length);
        // let nowIndex = routerList.indexOf(routerName);
        this.headerCofig.title = titleList[index];
        this.headerCofig.time = this.getNowDate();
      });
    }
  },
  mounted() {
    var _this = this
    this.setHeaderConfig();
    var swiper = new Swiper(".swiper-container", {
      autoplay: {
        delay: 40000,
        stopOnLastSlide: false,
        disableOnInteraction: true,
        waitForTransition: true
      },
      effect : 'fade',
      on: {
        slideChangeTransitionStart: function() {
          _this.setHeaderConfig(this.activeIndex)
        }
      }
    });
    this.setHeaderConfig(0)
  },
};
</script>
<style scoped>

body {
  position: relative;
  height: 100%;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
  /* display: none; */
  width: 3840px;
  height: 2160px;
  -webkit-background-size: 3840px 2160px;
  background-size: 3840px 2160px;
}

.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  background: rgba(12, 14, 36, 1);
}
.colee-box {
  width: 3840px;
  height: 130px;
  position: relative;
  bottom: 60px;
  left: 0;
  z-index: 99999;
  color: #fff;
  font-size: 60px;
}
</style>
